<template>
  <div>
    <!-- 導航欄 -->
    <van-nav-bar
      title="智慧商城"
    />
    <!-- 搜索框 -->
    <van-search @click="$router.push('/search')" v-model="search" placeholder="請輸入搜索關鍵詞" />
    <!-- 輪播圖 -->
    <van-swipe :autoplay="3000">
    <van-swipe-item v-for="item in bannerList" :key="item.imgUrl">
      <img :src="item.imgUrl" />
      </van-swipe-item>
    </van-swipe>
    <!-- 通知欄 -->
    <van-notice-bar scrollable text="學習vue2開發-使用vuex和router與axios以及vant"/>
    <!-- 宮格 -->
    <van-grid :column-num="5">
      <van-grid-item v-for="item in navList" :key="item.imgUrl" :icon="item.imgUrl" :text="item.text" />
    </van-grid>
    <!-- 猜你喜歡 -->
    <van-divider dashed>猜你喜歡</van-divider>
    <GoodsItem v-for="item in proList" :key="item.goods_id" :item="item"></GoodsItem>
    <div class="home_floor"></div>
  </div>
</template>

<script>
import GoodsItem from '@/components/GoodsItem.vue'
import { getHomeData } from '@/api/home'
export default {
  name: 'layoutHome',
  components: { GoodsItem },
  data () {
    return {
      search: '',
      images: [
        'https://img01.yzcdn.cn/vant/apple-1.jpg',
        'https://img01.yzcdn.cn/vant/apple-2.jpg'
      ],
      bannerList: [], // 輪播圖
      navList: [], // 宮格導航
      proList: [] // 猜你喜歡商品
    }
  },
  async created () {
    const { data: { pageData } } = await getHomeData()
    this.bannerList = pageData.items[1].data
    this.navList = pageData.items[3].data
    this.proList = pageData.items[6].data
  }
}
</script>

<style lang="less" scoped>
  .van-swipe-item{
    img{
      width: 94%;
    }
  }
  .home_floor{
    height: 65px;
  }
</style>
