<template>
  <div class="countBox">
    <button @click="totalDown">-</button>
    <input :value="value" @change="handleChange" type="text">
    <button @click="totalUp">+</button>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 1
    }
  },
  data () {
    return {
    }
  },
  methods: {
    handleChange (e) {
      const num = +e.target.value
      if (isNaN(num)) {
        e.target.value = this.value
        return
      } else if (num > 999) {
        e.target.value = 999
      } else if (num < 1) {
        e.target.value = 1
      } else {
        e.target.value = Math.floor(num)
      }
      this.$emit('input', e.target.value)
    },
    totalDown () {
      if (this.value > 1) {
        this.$emit('input', this.value - 1)
      }
    },
    totalUp () {
      if (this.value < 999) {
        this.$emit('input', this.value + 1)
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .countBox{
    border: 1px solid #999;
    border-radius: 5px;
    padding: 2px;
    button{
      width: 20px;
      padding: 0 3px;
      background-color: #fff;
      border: none;
      outline: none;
    }
    input{
      text-align: center;
      width: 38px;
      background-color: #fff;
      border: none;
      outline: none;
    }
  }
</style>
