import { getInfo, setInfo } from '@/utils/storage.js'

export default {
  namespaced: true,
  state () {
    return {
      userInfo: getInfo()
    }
  },
  mutations: {
    setUserInfo (state, obj) {
      state.userInfo = obj
      setInfo(obj)
    }
  },
  actions: {
    logout (context) {
      context.commit('setUserInfo', {})
      // 跨模塊的使用附加{ root: true }
      context.commit('cart/setCartList', [], { root: true })
    }
  },
  getters: {}
}
