import Vue from 'vue'
import VueRouter from 'vue-router'

import Layout from '@/views/layout/index.vue'
import LayoutHome from '@/views/layout/home.vue'
import LayoutApps from '@/views/layout/apps.vue'
import LayoutShopping from '@/views/layout/shopping.vue'
import LayoutUser from '@/views/layout/user.vue'
import store from '@/store/index.js'

const Login = () => import('@/views/login/index.vue')
const Search = () => import('@/views/search/index.vue')
const SearchList = () => import('@/views/search/list.vue')
const ProDetail = () => import('@/views/prodetail/index.vue')
const Pay = () => import('@/views/pay/index.vue')
const MyOrder = () => import('@/views/myorder/index.vue')
Vue.use(VueRouter)

const routes = [
  { path: '/login', component: Login },
  {
    path: '/',
    component: Layout,
    redirect: '/home',
    children: [
      { path: '/home', component: LayoutHome },
      { path: '/apps', component: LayoutApps },
      { path: '/shopping', component: LayoutShopping },
      { path: '/user', component: LayoutUser }
    ]
  },
  { path: '/search', component: Search },
  { path: '/searchlist', component: SearchList },
  { path: '/prodetail/:id', component: ProDetail },
  { path: '/pay', component: Pay },
  { path: '/myorder', component: MyOrder }
]
const router = new VueRouter({
  routes
})

const authUrls = ['/pay', '/myorder']
router.beforeEach((to, from, next) => {
  if (!authUrls.includes(to.path)) {
    next()
    return
  }
  // 如果是要登入才能進入的路由則確認store的user模塊的登入數據

  const token = store.state.user.userInfo.token
  if (token) {
    next()
  } else {
    next('/login')
  }
  // to.path 你所要前往的路由
  // next() ()直接放行 ('/')前往/路由
})

export default router
