<template>
  <div>
    <!-- 導航欄 -->
    <van-nav-bar
      title="全部分類"
    />
    <!-- 搜索框 -->
    <van-search @click="$router.push('/search')" placeholder="請輸入搜索關鍵詞" />
    <div class="appsBox">
      <div class="appsLeft">
        <div :class="{ 'fontRed': typeId === item.category_id }" class="leftItems" @click="clickType(item)" v-for="item in apps" :key="item.category_id">
          {{ item.name }}
        </div>
      </div>
      <div class="appsRight">
        <div v-show="!typeChildrens">暫無商品</div>
        <div class="rightItems" @click="goSearch(item.name)" v-for="item in typeChildrens" :key="item.image_id">
          <img :src="item.image.external_url" alt="">
          <p>{{ item.name }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getCategoryData } from '@/api/category'
import { getHistoryList, setHistoryList } from '@/utils/storage'

export default {
  name: 'layoutApps',
  data () {
    return {
      apps: [],
      typeId: '',
      typeChildrens: [],
      history: getHistoryList()
    }
  },
  async created () {
    const { data: { list } } = await getCategoryData()
    this.apps = list
    this.typeId = list[0].category_id
    this.typeChildrens = list[0].children
  },
  methods: {
    clickType (item) {
      this.typeId = item.category_id
      this.typeChildrens = item.children
    },
    goSearch (key) {
      if (key) {
        this.history = this.history.filter(i => i !== key)
        this.history.unshift(key)
        setHistoryList(this.history)
        this.$router.push(`/searchlist?search=${key}`)
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .fontRed{
    color: red;
    font-weight: bold;
  }
  *{
    overflow: hidden;
  }
  .appsBox{
    margin: 0 auto;
    width: calc(100vw - 28px);
    display: flex;
    align-items: flex-start;
    .appsLeft{
      width: 25%;
      height: calc(100vh - 150px);
      overflow: hidden;
      overflow-y: auto;
      .leftItems{
        margin: 20px 0;
      }
    }
    .appsRight{
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: flex-start;
      width: 75%;
      height: calc(100vh - 150px);
      overflow: hidden;
      overflow-y: auto;
      .rightItems{
        width: calc(33.3% - 10px);
        margin: 5px;
        img{
          width: 100%;
        }
      }
    }
  }
</style>
