import { getCartList, changeCount, delSelect } from '@/api/cart'
import { Toast } from 'vant'
export default {
  namespaced: true,
  state () {
    return {
      cartList: []
    }
  },
  mutations: {
    // 異步觸發事件_建立修改cartList數據的方法
    setCartList (state, newList) {
      state.cartList = newList
    },
    // 小勾選框點擊事件_依照id修改vuex的勾選數據
    toggleCheck (state, goodsId) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.isChecked = !goods.isChecked
    },
    // 全選框點擊事件_根據反轉後的全選框狀態改變每個小選框
    toggleAllCheck (state, flag) {
      state.cartList.forEach(item => {
        item.isChecked = flag
      })
    },
    // 數量框點擊事件_修改vuex的數據
    changeCount (state, { goodsId, value }) {
      const obj = state.cartList.find(item => item.goods_id === goodsId)
      obj.goods_num = value
    }
  },
  actions: {
    // 初始化數據_異步調用接口使用commit調用setCartList函式並傳參
    async getCartAction (context) {
      const { data } = await getCartList()
      // 對數據新增勾選框狀態
      data.list.forEach(item => {
        item.isChecked = true
      })
      context.commit('setCartList', data.list)
    },
    // 傳入數據並使用修改vuex的方法修改數量的數據
    async changeCountAction (context, obj) {
      const { goodsId, value, skuId } = obj
      context.commit('changeCount', {
        goodsId,
        value
      })
      // 使用api接口修改後台數據
      await changeCount(goodsId, value, skuId)
    },
    // 刪除購物車數據
    async delSelect (context) {
      // 被選中的商品
      const selCartList = context.getters.selCartList
      // 被選中的商品的Id
      const cartIds = selCartList.map(item => item.id)
      // 調用刪除商品的接口並傳入Id
      await delSelect(cartIds)
      Toast('刪除成功')

      // 重新渲染購物車數據
      context.dispatch('getCartAction')
    }
  },
  getters: {
    // 商品總數
    cartTotal (state) {
      return state.cartList.reduce((sum, item, index) => sum + item.goods_num, 0)
    },
    // 選中的商品列表
    selCartList (state) {
      return state.cartList.filter(item => item.isChecked)
    },
    // 選中的商品總數量
    selCount (state, getters) {
      return getters.selCartList.reduce((sum, item, index) => sum + item.goods_num, 0)
    },
    // 選中的商品總價格.toFixed(2)保留小數點第2位
    selPrice (state, getters) {
      return getters.selCartList.reduce((sum, item, index) => {
        return sum + item.goods_num * item.goods.goods_price_min
      }, 0).toFixed(2)
    },
    // 依照所有vuex的勾選數據來判斷是否全選
    isAllChecked (state) {
      return state.cartList.every(item => item.isChecked)
    }
  }
}
